$(".filter__show").click(function () {
    $(this).parents(".filter__group").toggleClass("_active")
    $(this).siblings(".filter__hidden").slideToggle()
})


$(".filter__group").each(function (index, el){
    if($(el).hasClass("_active")){
        $(el).find(".filter__hidden").css("display","block")
    }
});

$(".catalog__filter-toggle").click(function () {
    $(".catalog__sidebar").addClass("_active")
    $("body").addClass("fixed-body")
})

$(".filter__close").click(function () {
    $(".catalog__sidebar").removeClass("_active")
    $("body").removeClass("fixed-body")
})
