document.addEventListener('DOMContentLoaded', function(){

    const skinsSlider = new Swiper('.skins .swiper-container', {
        slidesPerView: 6,
        spaceBetween: 0,
        pagination: {
            el: '.skins .swiper-pagination',
            type: 'bullets',
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            450: {
                slidesPerView: 2,
            },
            620: {
                slidesPerView: 3,
            },
            800: {
                slidesPerView: 4,
            },
            1020: {
                slidesPerView: 5,
            },
            1170: {
                slidesPerView: 6,
            },
        }
    })
})

